export interface AIAssistantSettings {
  name: string
  subtitle: string
  avatar: string
  customAvatarUrl: string
  colorTheme: string
  customPrimaryColor: string
  customSecondaryColor: string
  personality: string
  customPersonalityPrompt: string
  welcomeMessage: string
  voiceEnabled: boolean
  voiceId: string
  responseLength: 'concise' | 'balanced' | 'detailed'
  proactiveMode: boolean
  showTypingIndicator: boolean
  soundEffects: boolean
  chatPosition: 'bottom-right' | 'bottom-left'
  chatWidth: number
  chatHeight: number
}

// Default settings
export const defaultAISettings: AIAssistantSettings = {
  name: 'Sandra',
  subtitle: 'AI Assistant',
  avatar: 'robot',
  customAvatarUrl: '',
  colorTheme: 'green',
  customPrimaryColor: '#10B981',
  customSecondaryColor: '#34D399',
  personality: 'friendly',
  customPersonalityPrompt: '',
  welcomeMessage: "Hi! I'm here to help you manage your CRM. What would you like to do today?",
  voiceEnabled: true,
  voiceId: 'alloy',
  responseLength: 'balanced',
  proactiveMode: true,
  showTypingIndicator: true,
  soundEffects: false,
  chatPosition: 'bottom-right',
  chatWidth: 384,
  chatHeight: 600,
}

interface AIAssistantContextType {
  settings: AIAssistantSettings
  updateSettings: (newSettings: Partial<AIAssistantSettings>) => void
  resetSettings: () => void
  getAvatarEmoji: () => string
  getPrimaryColor: () => string
  getSecondaryColor: () => string
}

const AIAssistantContext = createContext<AIAssistantContextType | undefined>(undefined)

const STORAGE_KEY = 'risivo-ai-assistant-settings'

export function AIAssistantProvider({ children }: { children: ReactNode }) {
  const [settings, setSettings] = useState<AIAssistantSettings>(defaultAISettings)
  const [isLoaded, setIsLoaded] = useState(false)

  // Load settings from localStorage on mount
  useEffect(() => {
    try {
      const stored = localStorage.getItem(STORAGE_KEY)
      if (stored) {
        const parsed = JSON.parse(stored)
        setSettings({ ...defaultAISettings, ...parsed })
      }
    } catch (e) {
      console.error('Failed to load AI assistant settings:', e)
    }
    setIsLoaded(true)
  }, [])

  // Save settings to localStorage when they change
  useEffect(() => {
    if (isLoaded) {
      try {
        localStorage.setItem(STORAGE_KEY, JSON.stringify(settings))
      } catch (e) {
        console.error('Failed to save AI assistant settings:', e)
      }
    }
  }, [settings, isLoaded])

  const updateSettings = (newSettings: Partial<AIAssistantSettings>) => {
    setSettings(prev => ({ ...prev, ...newSettings }))
  }

  const resetSettings = () => {
    setSettings(defaultAISettings)
  }

  const getAvatarEmoji = () => {
    const avatar = avatarOptions.find(a => a.id === settings.avatar)
    return avatar?.emoji || 'ü§ñ'
  }

  const getPrimaryColor = () => {
    if (settings.colorTheme === 'custom' || settings.customPrimaryColor !== defaultAISettings.customPrimaryColor) {
      return settings.customPrimaryColor
    }
    const theme = colorThemes.find(t => t.id === settings.colorTheme)
    return theme?.primary || settings.customPrimaryColor
  }

  const getSecondaryColor = () => {
    if (settings.colorTheme === 'custom' || settings.customSecondaryColor !== defaultAISettings.customSecondaryColor) {
      return settings.customSecondaryColor
    }
    const theme = colorThemes.find(t => t.id === settings.colorTheme)
    return theme?.secondary || settings.customSecondaryColor
  }

  return (
    <AIAssistantContext.Provider value={{
      settings,
      updateSettings,
      resetSettings,
      getAvatarEmoji,
      getPrimaryColor,
      getSecondaryColor,
    }}>
      {children}
    </AIAssistantContext.Provider>
  )
}

export function useAIAssistant() {
  const context = useContext(AIAssistantContext)
  if (context === undefined) {
    throw new Error('useAIAssistant must be used within an AIAssistantProvider')
  }
  return context
}
