'use client'

import { UserProvider } from '@/hooks/useUser'
import { AIAssistantProvider } from '@/lib/contexts/AIAssistantContext'

/**
 * Root Providers for RISIVO CRM
 * 
 * NOTE: SessionProvider from next-auth has been removed because:
 * 1. We use a custom JWT flow via /api/login (not next-auth)
 * 2. SessionProvider was calling /api/auth/session which couldn't decode our custom JWT
 * 3. This caused client-side redirects to /auth/signin on protected pages
 * 
 * User data is now managed via:
 * - UserProvider: Fetches user data from /api/user/profile
 * - useUser hook: Provides user data to components
 * 
 * AI Assistant settings are managed via:
 * - AIAssistantProvider: Provides AI assistant customization settings
 * - useAIAssistant hook: Provides settings to components
 */
export function Providers({ children }: { children: React.ReactNode }) {
  return (
    <UserProvider>
      <AIAssistantProvider>
        {children}
      </AIAssistantProvider>
    </UserProvider>
  )
}
