'use client'

import { createContext, useContext, useState, useEffect, ReactNode, useCallback } from 'react'

export interface User {
  id: string
  name: string
  email: string
  role: string
  isMasterAdmin: boolean
  isDemoUser: boolean
  agencyId: string | null
  agencyName: string
  avatar: string | null
  status: string
  company?: string | null
}

interface UserContextType {
  user: User | null
  isLoading: boolean
  error: string | null
  refetch: () => Promise<void>
}

const UserContext = createContext<UserContextType>({
  user: null,
  isLoading: true,
  error: null,
  refetch: async () => {},
})

export function UserProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  const fetchUser = useCallback(async () => {
    try {
      setIsLoading(true)
      setError(null)
      
      const response = await fetch('/api/user/profile', {
        credentials: 'include',
        cache: 'no-store',
      })

      if (!response.ok) {
        if (response.status === 401) {
          // Not authenticated - that's OK, just set user to null
          setUser(null)
          return
        }
        throw new Error('Failed to fetch user')
      }

      const data = await response.json()
      
      if (data.success && data.profile) {
        setUser({
          id: data.profile.id,
          name: `${data.profile.firstName || ''} ${data.profile.lastName || ''}`.trim() || 'User',
          email: data.profile.email,
          role: data.profile.role,
          isMasterAdmin: data.profile.isMasterAdmin || false,
          isDemoUser: data.profile.isDemoUser || data.profile.role === 'demo' || false,
          agencyId: data.profile.agencyId,
          agencyName: data.profile.agencyName || '',
          avatar: data.profile.avatar,
          status: data.profile.status || 'active',
          company: data.profile.company || null,
        })
      } else {
        setUser(null)
      }
    } catch (err: any) {
      console.error('[useUser] Error fetching user:', err)
      setError(err.message || 'Failed to fetch user')
      setUser(null)
    } finally {
      setIsLoading(false)
    }
  }, [])

  useEffect(() => {
    fetchUser()
  }, [fetchUser])

  return (
    <UserContext.Provider value={{ user, isLoading, error, refetch: fetchUser }}>
      {children}
    </UserContext.Provider>
  )
}

export function useUser() {
  const context = useContext(UserContext)
  if (!context) {
    throw new Error('useUser must be used within a UserProvider')
  }
  return context
}

/**
 * Simple hook to get user data without context (standalone fetch)
 * Use this for simple components that need user data on mount
 */
export function useUserData() {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    async function fetchUser() {
      try {
        const response = await fetch('/api/user/profile', {
          credentials: 'include',
          cache: 'no-store',
        })

        if (!response.ok) {
          if (response.status === 401) {
            setUser(null)
            return
          }
          throw new Error('Failed to fetch user')
        }

        const data = await response.json()
        
        if (data.success && data.profile) {
          setUser({
            id: data.profile.id,
            name: `${data.profile.firstName || ''} ${data.profile.lastName || ''}`.trim() || 'User',
            email: data.profile.email,
            role: data.profile.role,
            isMasterAdmin: data.profile.isMasterAdmin || false,
            isDemoUser: data.profile.isDemoUser || data.profile.role === 'demo' || false,
            agencyId: data.profile.agencyId,
            agencyName: data.profile.agencyName || '',
            avatar: data.profile.avatar,
            status: data.profile.status || 'active',
            company: data.profile.company || null,
          })
        }
      } catch (err: any) {
        console.error('[useUserData] Error:', err)
        setError(err.message)
      } finally {
        setIsLoading(false)
      }
    }

    fetchUser()
  }, [])

  return { user, isLoading, error }
}
